"use client";
import { Mic, StopCircle } from "lucide-react";
import { motion } from "framer-motion";
import { useState } from "react";

interface ButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
    className?: string;
}

export function Button({ children, className, ...props }: ButtonProps) {
    return (
        <button className={`px-4 py-2 rounded-lg text-white ${className}`} {...props}>
            {children}
        </button>
    );
}

interface CardProps {
    children: React.ReactNode;
    className?: string;
}

export function Card({ children, className }: CardProps) {
    return <div className={`bg-gray-800 p-4 rounded-lg shadow ${className}`}>{children}</div>;
}

export function CardContent({ children, className }: CardProps) {
    return <div className={`p-2 ${className}`}>{children}</div>;
}

interface Message {
    text: string;
    sender: "Olli" | "User";
}

export default function OlliAI() {
    const [isRecording, setIsRecording] = useState<boolean>(false);
    const [messages, setMessages] = useState<Message[]>([
        { text: "Hello! I'm Olli AI, your coding assistant. Press the mic to start talking.", sender: "Olli" }
    ]);

    const handleToggleRecording = () => {
        setIsRecording((prev) => !prev);
        if (!isRecording) {
            // Simulate response (replace with actual API call to ElevenLabs)
            setTimeout(() => {
                setMessages((prev) => [...prev, { text: "I heard your request! Processing...", sender: "Olli" }]);
            }, 2000);
        }
    };

    return (
        <div className="flex flex-col items-center justify-center min-h-screen p-4 bg-gray-900 text-white">
            <motion.h1 initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="text-3xl font-bold mb-6">
                Olli AI - Your Coding Assistant
            </motion.h1>
            <Card className="w-full max-w-2xl p-4">
                <CardContent className="h-80 overflow-y-auto flex flex-col gap-3">
                    {messages.map((msg, index) => (
                        <div
                            key={index}
                            className={`p-2 rounded-lg max-w-[75%] ${msg.sender === "Olli" ? "bg-blue-500 self-start" : "bg-gray-700 self-end"}`}
                        >
                            {msg.text}
                        </div>
                    ))}
                </CardContent>
            </Card>
            <div className="flex w-full max-w-2xl mt-4 justify-center">
                <Button onClick={handleToggleRecording} className={isRecording ? "bg-red-500" : "bg-green-500"}>
                    {isRecording ? <StopCircle size={24} /> : <Mic size={24} />}
                </Button>
            </div>
        </div>
    );
}